<template>
	<div id="workOrderInformation" class="content">
		<TopBar title='' @click-left="back">
			<template slot="right">
				<div v-show="showInstallModule" style="color: yellow;"   @click="installModuleClick">
					
				</div>
			</template>
		</TopBar>
		
		
		<van-form @submit="onSubmit" style="margin-bottom: 80px;">
			<van-field required readonly v-model="installedStatus" label=""  right-icon="warning-o"	/>
			<van-field readonly v-model="moduleCode"   label=""  />
			
			<van-field readonly v-model="userName" label=""  />
			<van-field readonly v-model="workDate" label=""  />
			<van-field readonly v-model="companyName" label=""  />
			<van-field readonly v-model="workshopName" label=""  />
			<van-field readonly v-model="equipmentStatusName" label=""  />
			<van-field readonly v-model="equipmentNo" label=""  />
			<van-field readonly v-model="equipmentName" label=""  />
			<van-field readonly v-model="categoryName" label=""  />
			<van-field readonly v-model="equipmentManagerName" label=""  />
			<van-field readonly v-model="phoneNum" label=""  />
			<van-field readonly v-model="wechatAccount" label=""  />
			
			<!-- <div  style="margin: 16px; bottom:0;width:92%;position:fixed">
				<van-button  plain round block type="info"  native-type="submit">
					
				</van-button>
			</div> -->
		</van-form>
	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'
	import Vue from 'vue'
	import {
		Cell,
		CellGroup,
		Toast,
		Notify
	} from 'vant'

	Vue.use(Cell)
	Vue.use(CellGroup)
	Vue.use(Notify);
	export default {
		name: 'WorkOrderInformation',
		components: {
			TopBar: TopBar
		},
		data() {
			return {
				id:"",
				moduleCode:"",
				workUserId:"",
				workDate:"",
				companyName:"",
				workshopName:"",
				equipmentStatus:"",
				equipmentStatusName:"",
				equipmentNo:"",
				equipmentName:"",
				categoryName:"",
				equipmentManagerName:"",
				phoneNum:"",
				wechatAccount:"",
				installed:"",
				installedStatus:"",
				back_url:"/main",
				showInstallModule:true
			}
		},
		created() {
			console.log("")
			console.log(JSON.stringify(this.$route.params));
			console.log(this.$route.params);
			console.log(this.$route.params.result);
			let instance = this;
			
			instance.combest.get(instance, "/iotapp/workOrder/queryWorkOrderInfo", {
					moduleCode: instance.$route.params.result
				}, function(response) {
					console.log(JSON.stringify(response))
					console.log("")
					console.log(response.data.data);
					console.log(JSON.stringify(response.data.data))
					console.log("")
					Object.assign(instance, response.data.data)
					instance.getInstallStatus(response.data.data.installed)
					instance.getEquipmentStatusName(response.data.data.equipmentStatus)
				},
				function(error) {
					console.log(error)
				}
			)
			
			// this.combest.get(this, "/iotapp/workOrder/isModuleWorkOrder", {
			// 		moduleCode: this.$route.params.result
			// 	}, function(response) {
			// 		console.log(JSON.stringify(response.data.data))
			// 		if(response.data.data == true){
						
			// 		}				
			// 	},
			// 	function(error) {
			// 		console.log(error)
			// })
			
		
		},
		methods: {
			back() {
				let instance = this;
				//
				if(this.$route.params.from == "main"){
					this.$router.push({
						name:'Home'
					})
				}else if(this.$route.params.from == "my"){
					this.$router.push({
						name:'My'
					})
				}else if(this.$route.params.from == "sysMessageInfo"){
					this.$router.push({
						name:'SysMessageInfo',
						params:{
							id:instance.$route.params.sysMessageId
						}
					})
				}
				// console.log(instance.back_url);
				// this.$router.push({
				// 	path:instance.back_url
				// })
			},
			
			
			/**
			 * 
			 */
			onSubmit() {
				
			},
			
			installModuleClick(){
				let instance = this;
				console.log("");
				// console.log(this.$data);
				instance.combest.get(instance, "/iotapp/workOrder/updateWorkOrder", {
					id: instance.id
				}, function(response) {
					console.log(JSON.stringify(response))
					if (response.data.code == 0) {
						Toast.success('!');
						Toast.setDefaultOptions({ duration: 1500 });
						instance.$router.push('/main');
					} else {
						Toast.setDefaultOptions({ duration: 1500 });
						Toast.success('!');
					}
				},
				function(error) {
					console.log(error)
				}, "application/json;charset=UTF-8")
			},
			
			/**
			 * @param {Object} value 
			 * 0
			 * 1
			 */
			getInstallStatus(value){
				console.log("");
				console.log(value)
				let instance = this;
				//
				if(instance.$route.params.from != "sysMessageInfo" && value == 0){
					instance.showInstallModule = false
					Notify({ type: 'warning', message: '' });
				}
				if(value == 0){
					instance.installedStatus = ""
					
				}else if(value == 1){
					instance.installedStatus = ""
					instance.showInstallModule = false
				}
			},
			
			/**
			 * @param {Object} value 
			 */
			getEquipmentStatusName(value){
				console.log("")
				console.log(value);
				let instance = this;
				if(value == "1"){
					instance.equipmentStatusName = ""
				}
				if(value == "2"){
					instance.equipmentStatusName = ""
				}
				if(value == "3"){
					instance.equipmentStatusName = ""
				}
			}
		}
	}
</script>

<style>
</style>
