<template>
	<div id="my" class="content">
		<TopBar title='' @click-left="back">
		</TopBar>
		<van-cell-group>
			<van-cell>
				<template #icon>
					<van-icon name="notes-o" class="timeIcon" />
				</template>

				<template #title>
					<van-field readonly v-model="title" label=":" />
				</template>
			</van-cell>
			<van-cell>
				<template #icon>
					<van-icon name="calendar-o" class="timeIcon" />
				</template>

				<template #title>
					<van-field v-model="date" placeholder="" readonly @click="showStart = true" />
					<van-popup v-model="showStart" position="bottom">
						<van-datetime-picker type="date" v-model="currentDate" @confirm="onStart" @cancel="showStart = false;date = ''"
						cancel-button-text="" ref="start" />
					</van-popup>
				</template>
				<template #default>
					<van-field name="time" placeholder="" readonly :value="time" clickable @click="showTimeFun" />
					<van-popup v-model="showTime" position="bottom">
						<van-picker show-toolbar :columns="timeColum" @confirm="onTime" @cancel="showTime = false" ref="time" />
					</van-popup>
				</template>
			</van-cell>
			<van-cell>
				<template #default>
					<van-field name="windowTypeName" label="" readonly :value="windowTypeName" clickable @click="showWindowType = true" />
					<van-popup v-model="showWindowType" position="bottom">
						<van-picker show-toolbar :columns="windowsTypeColum" @confirm="onWindowsType" @cancel="showWindowsType = false"
						ref="windowTypeName" />
					</van-popup>
				</template>
			</van-cell>
			<van-field v-model="deviceValue" is-link readonly placeholder="" @click="showDevice = true" v-if="showdeviceSelect" />
			<van-popup v-model="showDevice" round position="bottom">
				<van-cascader v-model="cascaderValue" title="" :options="options" @close="showDevice = false" @finish="onFinish" />
			</van-popup>
		</van-cell-group>
		<van-cell-group v-show="timeWaveDescShow">
			<van-cell>
					<van-field readonly v-model="selectNum" label="" />
			</van-cell>
			<van-cell>
					<van-field readonly v-model="selectTime" label="" />
			</van-cell>
			<van-cell>
					<van-field readonly v-model="selectValue" label="" />
			</van-cell>
		</van-cell-group>

		<div ref="timeWave" style="width:100%;height: 500px"></div>
		<van-cell-group v-show="windowWaveDescShow">
			<van-cell>
					<van-field readonly v-model="waveValue" label="" />
			</van-cell>
			<van-cell>
					<van-field readonly v-model="waveName" label="" />
			</van-cell>
		</van-cell-group>
		<div ref="windowWave" style="width:100%;height: 500px"></div>
		
	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'
	import Vue from 'vue'
	// import img from '@/assets/export.png'
	import {
		Cell,
		CellGroup,
		Field,
		DatetimePicker,
		Cascader,
		Checkbox,
		CheckboxGroup,
		Notify,
		Toast,
	} from 'vant'

	Vue.use(Cell);
	Vue.use(CellGroup);
	Vue.use(Field);
	Vue.use(DatetimePicker);
	Vue.use(Cascader);
	Vue.use(Checkbox);
	Vue.use(CheckboxGroup);
	Vue.use(Notify);
	Vue.use(Toast);
	export default {
		name: 'Test',
		components: {
			TopBar: TopBar,
		},
		data() {
			//console.log(this.id)
			return {
				id: 0,
				timeType: "",
				date: this.stringifyDate(),
				endTime: "",
				currentDate: new Date(),
				showStart: false,
				showEnd: false,
				showDevice: false,

				deviceValue: "",
				cascaderValue: "",
				options: [],

				projectTypeMap: {
					speed: '',
					acc: '',
					amplitude: '',
				},

				option: null,
				selectedOptions: [],
				lastTimeout: null,
				timeoutSpan: 1000 * 60 * 10,
				params: {},
				title: "",
				timeColum: [],
				showTime: false,
				time: "",
				showWindowType: false,
				windowType: "0",
				windowTypeName: "",
				windowsTypeColum: [{
						text: "",
						value: "0"
					},
					{
						text: "",
						value: "1"
					},
					{
						text: "",
						value: "2"
					}
				],
				// selectNum: "",
				// selectTime: "",
				// selectValue: "",
				// waveValue: "",
				// waveName: "",
				timeChart: "",
				windowChart: "",
				selectNum:"1.0",
				selectTime:"2021-01-04 11:58:50.225",
				selectValue:"5.3858194",
				waveValue:"0.0130716385",
				waveName:"220",
				timeWaveDescShow:false,
				windowWaveDescShow:false,
				showdeviceSelect: true,
				queryDataView:""
			}
		},
		created() {
			let instance = this;
			console.log("")
			console.log(this.$route.params.queryDataView);
			if(this.$route.params.queryDataView!=undefined){
				console.log(",");
				this.showdeviceSelect = false;
				this.queryDataView = this.$route.params.queryDataView;
				this.queryDataView.componentName = this.$route.params.queryDataView.componentName + this.$route.params.queryDataView.suffix
				this.title = this.queryDataView.componentName;
				console.log("this.title>>");
				console.log(this.title)
				this.setTimeValueFromTrendAnalysis(this.stringifyDate());
			}
			//console.log(this)
			//this.id = this.$route.params.id
			this.combest.get(this, "/company/ajaxGetComEquComZtree", {}, function(response) {
					console.log("")
					console.log(response)
					console.log(response.data)
					let zNodes = eval(response.data.data);
					instance.options = instance.formatTreeData(zNodes);
				},
				function(error) {
					console.log(error)

				})

		},
		methods: {
			back() {
				if(this.$route.params.queryDataView!=undefined){
					this.$router.push({
						name:"TrendAnalysis",
						params:{
							componentType:this.queryDataView.componentType,
							equipmentId:this.queryDataView.equipmentId,
							componentId:this.queryDataView.componentId,
							backUrl:'/monitoring/deviceList',
							name:this.queryDataView.name,
							equipmentStatus:this.queryDataView.equipmentStatus,
							componentName: this.queryDataView.componentName.split("_")[0],
							suffix:this.queryDataView.suffix,
						}
					})
				}else{
					this.$router.push({
						path: "/analysis"
					})
				}
				
				
			},
			toggleAll() {
				if (this.risk.includes("risk") && this.risk.includes("fault")) {
					this.$refs.riskGroup.toggleAll(false);
				} else {
					this.$refs.riskGroup.toggleAll(true);
				}
			},
			onStart() {
				let picker = this.$refs.start.getPicker();
				this.date = picker.getColumnValue(0) + '-' + picker.getColumnValue(1) + '-' + picker.getColumnValue(2);
				this.showStart = false;
			},
			showTimeFun() {
				if (this.timeColum.length == 0) {
					Notify({
						type: 'warning',
						message: ''
					});
				} else {
					this.showTime = true
				}
			},

			onTime(selected) {
			
				this.time = selected.text;
				// this.fromType = selected.value;
				this.$refs.time.setValues([selected]);
				this.showTime = false;
				if(this.$route.params.queryDataView != undefined){
					this.buildDataFromTrendAnalysis();
				}else{
					this.buildData();
				}
				
			},
			onWindowsType(selected) {
				this.windowTypeName = selected.text;
				this.windowType = selected.value;
				this.$refs.windowTypeName.setValues([selected]);
				this.showWindowType = false;
				if(this.$route.params.queryDataView != undefined){
					this.buildDataFromTrendAnalysis();
				}else{
					this.buildData();
				}
			},
			/**
			 * 
			 */
			buildDataFromTrendAnalysis(){
				console.log("");
				let instance = this;
				
				
				if (instance.$route.params.queryDataView != undefined) {
					if (instance.$route.params.queryDataView.componentType == undefined) {
						console.log("");
					} else {
						var time = instance.date + " " + instance.time
						console.log(":" + time);
						var date = instance.date;
						console.log("date:" + date);
						if (date == '' || date == null) {
							console.log(":");
							date = instance.getNowFormatDate();
							time = date + " " + instance.time;
							console.log(":" + time);
						}
				
						var windowType = instance.windowType
						var equipmentId = instance.$route.params.queryDataView.equipmentId;
						// var equipmentId = this.selectedOptions[this.selectedOptions.length - 3].id;
						var url = "/wave/data?componentId=" + instance.$route.params.queryDataView.componentId;
						url += "&type=" + instance.$route.params.queryDataView.componentType;
						url += "&time=" + time;
						url += "&equipmentId=" + equipmentId;
						url += "&windowType=" + windowType;
						console.log("url>>")
						console.log(url);
						instance.combest.get(instance, url, {},
							function(response) {
								console.log("echart")
								console.log(response);
								console.log(response.data);
								if (response.data != null && response.data != "") {
									console.log("")
									instance.timeWaveChart(response.data.data)
									instance.waveChart(response.data.result, response.data.samplingRate)
								} else {
									console.log("")
									instance.timeWaveChart([])
									instance.waveChart([], 2048)
								}
							},
							function(error) {
								console.log(error)
							});
					}
				} else {
					return false;
				}
			},
			/**
			 * 
			 */
			buildData() {
				console.log("");
				let instance = this;
				let checkedNode = this.selectedOptions[this.selectedOptions.length - 1];
				if (checkedNode != undefined) {
					if (checkedNode.componentType == undefined) {
						console.log("");
					} else {
						var time = instance.date + " " + instance.time
						console.log(":" + time);
						var date = instance.date;
						console.log("date:" + date);
						if (date == '' || date == null) {
							console.log(":");
							date = instance.getNowFormatDate();
							time = date + " " + instance.time;
							console.log(":" + time);
						}

						var windowType = instance.windowType
						var equipmentId = this.getParentEquipmentId(checkedNode);
						// var equipmentId = this.selectedOptions[this.selectedOptions.length - 3].id;
						var url = "/wave/data?componentId=" + checkedNode.id;
						url += "&type=" + checkedNode.componentType;
						url += "&time=" + time;
						url += "&equipmentId=" + equipmentId;
						url += "&windowType=" + windowType;
						console.log("url>>")
						console.log(url);
						instance.combest.get(instance, url, {},
							function(response) {
								console.log("echart")
								console.log(response);
								console.log(response.data);
								if (response.data != null && response.data != "") {
									console.log("")
									instance.timeWaveChart(response.data.data)
									instance.waveChart(response.data.result, response.data.samplingRate)
								} else {
									console.log("")
									instance.timeWaveChart([])
									instance.waveChart([], 2048)
								}
							},
							function(error) {
								console.log(error)
							});
					}
				} else {
					return false;
				}

			},

			getEchartData() {
				const timeWave = this.$refs.timeWave
				const windowWave = this.$refs.windowWave
				if (timeWave) {
					this.timeChart = this.$echarts.init(timeWave);
				}
				if (windowWave) {
					this.windowChart = this.$echarts.init(windowWave);
				}
			},
			formatTreeData(rawData) {
				function formatNodeList(nodeList) {
					for (var i = 0, len = nodeList.length; i < len; i += 1) {
						var node = nodeList[i];
						setupOption(node);
						setupCheckbox(node);
						setupIcon(node);

						if (node.children && node.children.length) {
							formatNodeList(node.children);
						}
					}
				}

				function setupCheckbox(node) {
					var isModule = !!node.componentType;
					if (!isModule) {
						node.nocheck = true;
					}
				}

				function setupOption(node) {
					node.value = node.name;
					node.text = node.name;
				}

				// 
				// X/Y/

				// public final static int STATUS_WORKING=1;//
				// public final static int STATUS_STOP=2;//
				// public final static int STATUS_TAKE=3;//
				// public final static int STATUS_UNUSUAL=4;//
				// public final static int STATUS_HIGHRISK=5;//
				// public final static int STATUS_FAULT=6;// ()
				// public final static int STATUS_OFFLINE=7;//
				function setupStatus(nodeList, parentStatistics) {
					if (!nodeList || !nodeList.length) {
						return;
					}

					var runningStatus = {
						working: 1,
						stop: 2,
						maintain: 3,
						abnormal: 4,
						risk: 5,
						fault: 6,
						offline: 7,
					};

					var iconStatus = {
						working: 'working',
						stop: 'stop',
						maintain: 'maintain',
						abnormal: 'abnormal',
						fault: 'fault',
						risk: 'risk',
						offline: 'offline',
					};

					for (var i = 0, len = nodeList.length; i < len; i += 1) {
						var node = nodeList[i];

						if (parentStatistics) {
							parentStatistics.total += 1;
						}

						// type: 123
						if (node.type === 1) {
							var statistics = {
								total: 0,
								working: 0,
								stop: 0,
								maintain: 0,
								abnormal: 0,
								fault: 0,
								risk: 0,
								offline: 0,
							};
							setupStatus(node.children, statistics);

							// offline > abnormal > fault > risk > maintain > stop > working
							if (statistics.offline) {
								node.status = iconStatus.offline;
								parentStatistics && (parentStatistics.offline += 1);
							} else if (statistics.abnormal) {
								node.status = iconStatus.abnormal;
								parentStatistics && (parentStatistics.abnormal += 1);
							} else if (statistics.fault) {
								node.status = iconStatus.fault;
								parentStatistics && (parentStatistics.fault += 1);
							} else if (statistics.risk) {
								node.status = iconStatus.risk;
								parentStatistics && (parentStatistics.risk += 1);
							} else if (statistics.maintain) {
								node.status = iconStatus.maintain;
								parentStatistics && (parentStatistics.maintain += 1);
							} else if (statistics.stop) {
								node.status = iconStatus.stop;
								parentStatistics && (parentStatistics.stop += 1);
							} else {
								node.status = iconStatus.working;
								parentStatistics && (parentStatistics.working += 1);
							}
						}
						//  
						else if (node.type === 2) {
							setupStatus(node.children);
							// 
							if (node.equipmentStatus === runningStatus.working) {
								node.status = iconStatus.working;
								parentStatistics && (parentStatistics.working += 1);
							} else if (node.equipmentStatus === runningStatus.stop) {
								node.status = iconStatus.stop;
								parentStatistics && (parentStatistics.stop += 1);
							} else if (node.equipmentStatus === runningStatus.maintain) {
								node.status = iconStatus.maintain;
								parentStatistics && (parentStatistics.maintain += 1);
							} else if (node.equipmentStatus === runningStatus.abnormal) {
								node.status = iconStatus.abnormal;
								parentStatistics && (parentStatistics.abnormal += 1);
							} else if (node.equipmentStatus === runningStatus.risk) {
								node.status = iconStatus.risk;
								parentStatistics && (parentStatistics.risk += 1);
							} else if (node.equipmentStatus === runningStatus.fault) {
								node.status = iconStatus.fault;
								parentStatistics && (parentStatistics.fault += 1);
							} else if (node.equipmentStatus === runningStatus.offline) {
								node.status = iconStatus.offline;
								parentStatistics && (parentStatistics.offline += 1);
							}
						} else {
							// 
							setupStatus(node.children);
							node.status = iconStatus.offline;
							parentStatistics && (parentStatistics.offline += 1);
						}
					}
				}

				// 
				function setupIcon(node) {
					// type: 123
					switch (node.type) {
						case 1:
							node.icon = '/img/monitor/ic_company_' + node.status + '.png';
							break;
						case 2:
							node.icon = '/img/monitor/ic_device_' + node.status + '.png';
							break;
						default:
							node.icon = '/img/monitor/ic_device_' + node.status + '.png';
					}
				}

				setupStatus(rawData);
				formatNodeList(rawData);

				return rawData;
			},

			getParentEquipmentId(moduleNode) {
				if (!moduleNode) {
					return '';
				}
				if (moduleNode.index < 2) {
					return '';
				}
				var componentNode = this.selectedOptions[moduleNode.index - 1];

				if (!componentNode) {
					return '';
				}

				var equipmentNode = this.selectedOptions[moduleNode.index - 2];

				return (equipmentNode && equipmentNode.id) || '';
			},
			onFinish({
				selectedOptions
			}) {
				this.showDevice = false;
				this.deviceValue = selectedOptions.map((option) => option.text).join(' > ');
				this.selectedOptions = selectedOptions;
				let checkedNode = selectedOptions[selectedOptions.length - 1];
				checkedNode.index = selectedOptions.length - 1;
				console.log(selectedOptions)
				if (checkedNode) {
					if (checkedNode.componentType == undefined) {
						console.log("no")
					} else {
						// var em = this.projectTypeMap['speed'];
						// this.deviceValue = this.deviceValue + ' > ' + em;
						this.title = checkedNode.name
						this.setTimeValue(this.stringifyDate());
					}
				} else {
					return false;
				}
			},
			/**
			 * 
			 * @param {Object} value 
			 */
			setTimeValueFromTrendAnalysis(value) {
				let instance = this;
				console.log("");
				console.log(instance.$route.params.queryDataView);
				
				if (instance.$route.params.queryDataView != undefined) {
					if (instance.$route.params.queryDataView.componentType == undefined) {
						//   lay.msg( "");
						// layer.msg("")
					} else {
			
						// var equipmentId = this.selectedOptions[this.selectedOptions.length - 3].id;
						var equipmentId = instance.$route.params.queryDataView.equipmentId
						
						var timeUrl = "/wave/timeByDate?componentId=" + instance.$route.params.queryDataView.componentId + "&date=" + value;
						timeUrl += "&equipmentId=" + equipmentId;
						console.log("timeUrl>>");
						console.log(timeUrl)
						//
						instance.combest.get(instance, timeUrl, {},
							function(response) {
								console.log("");
								console.log("")
								console.log(response);
								console.log(response.data.data)
								console.log(response.data.date == undefined);
								if (response.data.date == null || response.data.date == undefined) {
									//,
									
									Notify({
										type: 'warning',
										message: '[' + value + ']!'
									});
									instance.timeColum = []; //
									if (instance.timeColum.length == 0) {
										instance.time = "";
									}
									instance.buildDataFromTrendAnalysis();
								} else {
									console.log("");
									console.log(response);
									var list = response.data.list;
									var date = response.data.date;
									if (value != date) {
										//  + 
										instance.date = date;
										Notify({
											type: 'warning',
											message: '[' + value + '], :[' + date + ']'
										});
									}
									instance.timeColum = []; //
									for (var i = 0; i < list.length; i++) {
										let temp = {};
										temp.text = list[list.length - i - 1];
										temp.value = list[list.length - i - 1];
										instance.timeColum.push(temp);
									}
									console.log("");
									console.log(instance.timeColum);
									console.log(instance.timeColum.length);
									if (instance.timeColum.length == 0) {
										instance.time = ""
										console.log("")
									} else {
										console.log("")
										instance.time = instance.timeColum[0].text;
									}
			
									instance.buildDataFromTrendAnalysis();
								}
							},
							function(error) {
								console.log(error)
							});
					}
				} else {
					return false;
				}
			},
			/**
			 * @param {Object} value 
			 */
			setTimeValue(value) {
				let instance = this;
				var checkedNode = this.selectedOptions[this.selectedOptions.length - 1];
				console.log("checkedNode>>")
				console.log(checkedNode);
				console.log("selectedOptions")
				if (checkedNode != undefined) {
					if (checkedNode.componentType == undefined) {
						//   lay.msg( "");
						// layer.msg("")
					} else {

						// var equipmentId = this.selectedOptions[this.selectedOptions.length - 3].id;
						var equipmentId = this.getParentEquipmentId(checkedNode);

						var timeUrl = "/wave/timeByDate?componentId=" + checkedNode.id + "&date=" + value;
						timeUrl += "&equipmentId=" + equipmentId;
						//
						instance.combest.get(instance, timeUrl, {},
							function(response) {
								console.log("");
								console.log("")
								console.log(response);
								console.log(response.data.data)
								if (response.data.date == null || response.data.date == undefined) {
									//,
									Notify({
										type: 'warning',
										message: '[' + value + ']!'
									});
									instance.timeColum = []; //
									if (instance.timeColum.length == 0) {
										instance.time = "";
									}
									instance.buildData();
								} else {
									console.log("");
									console.log(response);
									var list = response.data.list;
									var date = response.data.date;
									if (value != date) {
										//  + 
										instance.date = date;
										Notify({
											type: 'warning',
											message: '[' + value + '], :[' + date + ']'
										});
									}
									instance.timeColum = []; //
									for (var i = 0; i < list.length; i++) {
										let temp = {};
										temp.text = list[list.length - i - 1];
										temp.value = list[list.length - i - 1];
										instance.timeColum.push(temp);
									}
									console.log("");
									console.log(instance.timeColum);
									console.log(instance.timeColum.length);
									if (instance.timeColum.length == 0) {
										instance.time = ""
										console.log("")
									} else {
										console.log("")
										instance.time = instance.timeColum[0].text;
									}

									instance.buildData();
								}
							},
							function(error) {
								console.log(error)
							});
					}
				} else {
					return false;
				}
			},
			stringifyDate() {
				let date = new Date();

				if (date.toString() === 'Invalid Date') {
					return '';
				}

				function padding(num) {
					return num >= 10 ? '' + num : '0' + num;
				}

				var str = '';

				str += date.getFullYear();
				str += '-' + padding(date.getMonth() + 1);
				str += '-' + padding(date.getDate());

				return str;
			},
			stringifyTime(date) {
				if (date.toString() === 'Invalid Date') {
					return '';
				}

				function padding(num) {
					return num >= 10 ? '' + num : '0' + num;
				}

				var str = '';

				str += date.getFullYear();
				str += '-' + padding(date.getMonth() + 1);
				str += '-' + padding(date.getDate());
				str += ' ' + padding(date.getHours());
				str += ':' + padding(date.getMinutes());
				str += ':' + padding(date.getSeconds());

				return str;
			},
			getNowFormatDate() {
				var date = new Date();
				var seperator1 = "-";
				var year = date.getFullYear();
				var month = date.getMonth() + 1;
				var strDate = date.getDate();
				if (month >= 1 && month <= 9) {
					month = "0" + month;
				}
				if (strDate >= 0 && strDate <= 9) {
					strDate = "0" + strDate;
				}
				var currentdate = year + seperator1 + month + seperator1 + strDate;
				return currentdate;
			},

			timeWaveChart(data) {
				let instance = this;
				console.log("");
				console.log(data);
				console.log(data.length);
				/*var data={};
				for(var i=0;i<arr.length;i++){
					data[i+1]=arr[i];
				}*/


				var timeX = [];
				for (var i = 0; i < data.length; i++) {
					// timeX[i]=i+1;
					//
					timeX[i] = Math.round(i + 1);
				}



				var option = {
					title: {
						text: "",
						x: "center",
						y: "top"

					},
					tooltip: {
						trigger: 'item',
						enterable:true,
						position: function(point, params, dom, rect, size) {
							// divxy
							// 
							var x = 0; // x
							var y = 0; // y

							// 
							var pointX = point[0];
							var pointY = point[1];

							// div
							// var viewWidth = size.viewSize[0];
							// var viewHeight = size.viewSize[1];

							// 
							var boxWidth = size.contentSize[0];
							var boxHeight = size.contentSize[1];

							// boxWidth > pointX 
							if (boxWidth > pointX) {
								x = 5;
							} else { // 
								x = pointX - boxWidth;
							}

							// boxHeight > pointY 
							if (boxHeight > pointY) {
								y = 5;
							} else { // 
								y = pointY - boxHeight;
							}

							return [x, y];
						},

						show: true,
						triggerOn: 'mousemove',
						backgroundColor: "#009688",
						formatter: function(params, ticket, callback) {
							console.log("");
							
							
							console.log(params, ticket, callback)
							var num = params.name;
							var time = instance.date + " " + instance.time
							if (num < 1024) {
								time += "." + (1000 + parseInt(num * 1000 / 1024) + "").substring(1)
							} else {
								var date = new Date(time);
								date.setSeconds(date.getSeconds() + 1);
								time = instance.stringifyTime(date);
								time += "." + (1000 + parseInt((num - 1024) * 1000 / 1024) + "").substring(1)
							}
							var text = ":" + params.value;
							text += "</br>:" + params.name;
							text += "</br>:" + time;
							
							
							instance.timeWaveDescShow = true;
							instance.selectValue =  params.value;		//
							instance.selectNum = params.name;			//
							instance.selectTime = time;					//
							instance.windowWaveDescShow = false;
							
							return text;
						}
					},
					dataZoom: [{
						type: "inside",
						start: 1,
						end: 100
					}],
					toolbox: { //
						show: true,
						feature: {
							dataView: { //
								show: true
							},
							restore: { //
								show: false
							},
							saveAsImage: { //
								show: false
							}
						}
					},
					xAxis: {
						type: 'category',
						name: 'ms',
						data: timeX
					},
					yAxis: {
						type: 'value',
						name: 'm',
					},
					series: [{
						data: data,
						type: 'line',
						itemStyle: {
							normal: {
								lineStyle: {
									color: '#009688'
								}
							}
						},
					}]
				};

				setTimeout(() => {
					try {
						instance.timeChart.setOption(option, true)
					} catch (error) {
						console.log("");
					}
				}, 500)
			},
			waveChart(data, fs) {
				console.log("")
				console.log(data);
				console.log(fs);
				let instance = this;
				var count = data.length;
				var x = [];
				// console.log(data);
				var length = data.length >= 1000 ? 1000 : data.length;
				console.log("length>>" + length);
				for (var i = 0; i < length; i++) {
					// x[i]=(i)*fs/count;
					//
					x[i] = Math.round((i) * fs / count);
				}
				console.log(x);
				var windowType = instance.windowType;
				var text = "";
				if (windowType == "1") {
					text = "";
				} else if (windowType == "2") {
					text = "";
				}
				var waveOption = {
					title: {
						text: "-" + text,
						x: "center",
						y: "top"
					},

					tooltip: {
						trigger: 'item',
						show: true,
						enterable:true,
						triggerOn: 'mousemove',
						position: function(point, params, dom, rect, size) {
							// divxy
							// 
							var x = 0; // x
							var y = 0; // y
						
							// 
							var pointX = point[0];
							var pointY = point[1];
						
							// div
							// var viewWidth = size.viewSize[0];
							// var viewHeight = size.viewSize[1];
						
							// 
							var boxWidth = size.contentSize[0];
							var boxHeight = size.contentSize[1];
						
							// boxWidth > pointX 
							if (boxWidth > pointX) {
								x = 5;
							} else { // 
								x = pointX - boxWidth;
							}
						
							// boxHeight > pointY 
							if (boxHeight > pointY) {
								y = 5;
							} else { // 
								y = pointY - boxHeight;
							}
						
							return [x, y];
						},
						formatter: function(params, ticket, callback) {
							console.log("")
							console.log(params, ticket, callback);
							var text = ":" + params.value;
							text += "</br>:" + params.name;
							
							instance.windowWaveDescShow = true;
							instance.waveValue =  params.value;			//
							instance.waveName = params.name;			//
							instance.timeWaveDescShow = false;

							return text;
						}
					},
					dataZoom: [{
						type: "inside",
						start: 1,
						end: 100
					}],
					toolbox: { //
						show: true,
						feature: {
							dataView: { //
								show: true
							},
							restore: { //
								show: false
							},

							saveAsImage: { //
								show: false
							}
						}
					},
					xAxis: {
						type: 'category',
						name: 'Hz',
						data: x
					},
					yAxis: {
						type: 'value',
						name: 'm',
					},
					series: [{
						data: data,
						type: 'line',
						itemStyle: {
							normal: {
								lineStyle: {
									color: '#009688'
								}
							}
						},
					}]
				};

				setTimeout(() => {
					try {
						instance.windowChart.setOption(waveOption, true)
					} catch (error) {
						console.log("");
					}
				}, 500)
			}

		},
		mounted() {
			this.getEchartData()
		},
	}
</script>

